.description {
  width: 100%;
  min-height: 878px;
  padding-bottom: 258px;
  background: url("../img/bg/description/description-bg-desktop.png") no-repeat,
    url("../img/bg/description/description-bg-triangles-desktop.png") no-repeat,
    url("../img/bg/description/description-bg-main-desktop.jpg") no-repeat;
  background-position: 85.5% 258px, 0, 0;
  background-size: 36.7%, 100%, cover;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/bg/description/description-bg-desktop@2x.png"),
      url("../img/bg/description/description-bg-triangles-desktop@2x.png"),
      url("../img/bg/description/description-bg-main-desktop@2x.jpg");
  }

  padding-left: 3.1rem;
  padding-right: 3.1rem;

  .description__wrap {
    max-width: 1425px;
    padding: 1rem 0.43rem;
    margin: 0 auto;
  }

  .description__block_text {
    margin-top: 305px;
    margin-left: 0;
    font-family: $title-bold-font;
    color: $main-color-dark-block;
    position: relative;
  }

  .description__block_text h2 {
    width: 718px;
    text-transform: uppercase;
    font-size: 79px;
    margin-left: -4px;
    letter-spacing: 7.2px;
  }

  .description__block_text p {
    width: 615px;
    font-size: 22px;
    margin-top: 36px;
    margin-left: 94px;
    line-height: 34px;
    position: relative;
    font-family: $main-font;

    &::before {
      content: "";
      position: absolute;
      background: $main-color-dark-block;
      width: 66px;
      height: 2px;
      top: 17px;
      left: -94px;
    }
  }

  .description__btn {
    display: block;
    width: 406px;
    margin-top: 46px;
    margin-left: 94px;
    padding: 21px 10px 20px 8px;
    text-align: center;
    text-transform: uppercase;
    background: $auxiliary-color;
    border-radius: 5px;
    letter-spacing: 1px;

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.8;
    }
  }

  .description__btn_text_mobile {
    display: none;
  }

  .description__btn_text_tablet {
    display: block;
  }

  .description__scroll {
    display: block;
    font-family: $main-font;
    font-size: 14px;
    opacity: 0.5;
    transform: rotate(-90deg);
    position: absolute;
    bottom: -147px;
    left: -38px;
  }

  @media (max-width: $desktop-width) {
    min-height: 686px;
    padding-bottom: 0;

    background: url("../img/bg/description/description-bg-tablet.png") 100% 32.6% no-repeat,
      url("../img/bg/description/description-bg-main-tablet.jpg")  0 0 / 100% no-repeat;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/bg/description/description-bg-tablet@2x.png"),
        url("../img/bg/description/description-bg-main-tablet@2x.jpg");
      background-size: 33.8%, 100%;
    }

    .description__wrap {
      max-width: 720px;
      padding: 1rem 0.95rem;
    }

    .description__block_text {
      margin: 172px 0 0 27px;
    }

    .description__block_text h2 {
      font-size: 60px;
      line-height: 70px;
      letter-spacing: 5.2px;
      width: 52%;
    }

    .description__block_text p {
      width: 356px;
      font-size: 18px;
      line-height: 27px;
      margin: 34px 0 0 47px;

      &::before {
        width: 33px;
        left: -48px;
        top: 14px;
      }
    }

    .description__btn {
      width: 379px;
      margin-top: 55px;
      margin-left: 47px;
      padding: 20px 9px 20px 8px;
      letter-spacing: 1px;
    }

    .description__scroll {
      display: none;
    }
  }

  @media (max-width: $tablet-width) {
    min-height: 648px;
    padding: 0;
    background-color: #1b2d37;
    background-image: url("../img/bg/description/description-bg-mobile.png"),
      url("../img/bg/description/triangle-mobile.svg"),
      url("../img/bg/description/description-bg-main-mobile.png");
    background-repeat: no-repeat, no-repeat, no-repeat;
    background-position: 100.5% 21.5%, 167px 70px;
    background-size: 294px, 461px, 100%;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/bg/description/description-bg-mobile@2x.png"),
        url("../img/bg/description/triangle-mobile.svg"),
        url("../img/bg/description/description-bg-main-mobile@2x.png");
    }

    .description__wrap {
      max-width: 320px;
    }

    .description__block_text {
      margin: 270px auto 0 auto;
    }

    .description__block_text h2 {
      width: 245px;
      font-size: 2.5rem;
      line-height: 46.88px;
      letter-spacing: 3.5px;
      margin-left: 0;
    }

    .description__block_text p {
      width: 226px;
      font-size: 1rem;
      margin: 34px 0 0 51px;
      line-height: 24px;

      &::before {
        width: 33px;
        left: -48px;
        top: 14px;
      }
    }

    .description__btn {
      width: 287px;
      margin: 42px auto 0;
      letter-spacing: 0.07rem;
      padding: 19px 5px 20px 8px;
    }

    .description__btn_text_mobile {
      display: block;
    }

    .description__btn_text_tablet {
      display: none;
    }
  }
}
