.feedback {
  background: $auxiliary-color;
  color: $main-color-dark-block;
  font-family: $main-font;
  padding-top: 99px;
  padding-bottom: 78px;
  background-image: url("../img/bg/form/triangle-bg-rigth-bot.png"),
    url("../img/bg/form/triangle-bg-rigth-top.png");
  background-repeat: no-repeat, no-repeat;
  background-position: 1576px 126%, 1640px 11%;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/bg/form/triangle-bg-rigth-bot@2x.png"),
      url("../img/bg/form/triangle-bg-rigth-top@2x.png");
    background-size: 18%, 14%;
  }

  .feedback__wrap {
    max-width: 1220px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 0 24px 0 0;
    margin: 0 auto;
  }

  .feedback__block_text {
    width: 48%;
    margin: 0;
  }

  .feedback__block_text h3 {
    font-family: $title-font;
    line-height: 37px;
    font-size: 36px;
    margin-bottom: 18px;
  }

  .feedback__block_text p {
    font-family: $main-font;
    line-height: 22px;
    margin-bottom: 49px;
  }

  .feedback__form {
    width: 48%;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: -178px;
      left: -824px;
      width: 142%;
      height: 535px;
      background-image: url("../img/bg/form/plane.png"),
        url("../img/bg/form/triangle-bg-left-small.png"),
        url("../img/bg/form/triangle-bg-left-big.png");
      background-repeat: no-repeat, no-repeat, no-repeat;
      background-position: 66% 47%, 6% 99%, 34% 101%;

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url("../img/bg/form/plane@2x.png"),
          url("../img/bg/form/triangle-bg-left-small@2x.png"),
          url("../img/bg/form/triangle-bg-left-big@2x.png");
        background-size: 62%, 46%, 78%;
      }
    }
  }

  .feedback__block_input {
    margin-bottom: 39px;
    display: flex;
    justify-content: space-between;
  }

  .feedback__input,
  .feedback__input-text {
    color: $main-color-dark-block;
    border: none;
    border-radius: 0;
    font-family: $main-font;
    background: rgba(255, 255, 255, 0.1);
    padding: 5px 20px 9px 20px;
    font-size: 16px;

    &::placeholder {
      font-family: $main-font;
      color: $main-color-dark-block;
    }
  }

  .feedback__input {
    width: 47.5%;
    height: 51.83px;
  }

  .feedback__input-text {
    width: 100%;
    height: 154px;
    padding-top: 13px;
    margin-bottom: 41px;
    resize: none;
    overflow: auto;
  }

  .feedback__block_submit {
    display: flex;
    justify-content: space-between;
  }

  .feedback__submit {
    @include button-reset;
    width: 48%;
    border: 1px solid $main-color-dark-block;
    border-radius: 5px;
    font-family: $title-bold-font;
    font-size: 0.9rem;
    text-transform: uppercase;
    letter-spacing: 0.06rem;
    padding: 0.9rem 1rem;
    margin-bottom: 30px;

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.8;
    }
  }

  .feedback__block_check {
    display: flex;
    align-items: flex-start;
    width: 50%;
    padding: 8px 0 0 24px;
  }

  .feedback__block_check p {
    margin: 0 0 0 16px;
    width: 76%;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.8);
  }

  .feedback__check {
    &:hover + .feedback__check_label,
    &:focus + .feedback__check_label {
      border-color: rgba($color: $main-color-dark-block, $alpha: 0.6);
    }
  }

  .feedback__check_label {
    width: 17px;
    height: 16px;
    border: 1px solid $main-color-dark-block;
    background: transparent;
    cursor: pointer;
    margin: 1px 0 0 2px;
    position: relative;
    font-size: 0;
    line-height: 0;
  }

  .feedback__check:checked + &__check_label {
    &::before {
      content: "";
      position: absolute;
      width: 5px;
      height: 5px;
      border-top: 1px solid $main-color-dark-block;
      border-left: 1px solid $main-color-dark-block;
      top: 25%;
      left: 27%;
      transform: rotate(-140deg);
    }
  }

  @media (max-width: $desktop-width) {
    padding-top: 84px;
    padding-bottom: 71px;

    .feedback__wrap {
      padding: 0 5.5rem;
      max-width: 942px;
    }

    .feedback__block_text {
      width: 100%;
    }

    .feedback__block_text h3 {
      font-size: 32px;
      margin-bottom: 20px;
    }

    .feedback__block_text p {
      margin-left: 5px;
      margin-bottom: 53px;
    }

    .feedback__form {
      width: 100%;

      &::before {
        display: none;
      }
    }

    .feedback__block_input {
      justify-content: space-between;
      flex-direction: row;
      margin-bottom: 43px;
    }

    .feedback__input {
      padding-left: 20px;
      padding-right: 20px;
      margin-bottom: 0;
    }

    .feedback__input-text {
      padding-left: 20px;
      padding-right: 20px;
      height: 159.63px;
      margin-bottom: 41px;
    }

    .feedback__block_submit {
      display: flex;
      justify-content: space-between;
    }

    .feedback__submit {
      width: 48%;
    }

    .feedback__block_check {
      width: 50%;
      padding: 08px 0 0 24px;
    }

    .feedback__block_check p {
      margin-left: 16px;
      line-height: 16px;
    }
  }

  @media (max-width: $tablet-width) {
    background: $auxiliary-color;
    color: $main-color-dark-block;
    font-family: $main-font;
    padding-top: 32px;
    padding-bottom: 39px;

    .feedback__wrap {
      padding: 0 0.9rem;
      max-width: 458px;
    }

    .feedback__block_text {
      margin: 0 auto;
    }

    .feedback__block_text h3 {
      font-family: $title-font;
      font-size: 28px;
      line-height: 37px;
      margin-bottom: 14px;
    }

    .feedback__block_text p {
      font-family: $main-font;
      line-height: 22px;
      margin-bottom: 27px;
      margin-left: 0;
    }

    .feedback__block_input {
      display: flex;
      flex-direction: column;
      margin-bottom: 0;
    }

    .feedback__input,
    .feedback__input-text {
      width: 100%;
      margin-bottom: 30px;
      padding: 5px 17px 9px;
    }

    .feedback__input-text {
      height: 104px;
      padding-top: 13px;
      margin-bottom: 28px;
      resize: none;
    }

    .feedback__block_submit {
      display: block;
    }

    .feedback__submit {
      width: 100%;
      font-size: 0.9rem;
      padding: 0.9rem 1rem;
    }

    .feedback__block_check {
      width: 100%;
      display: flex;
      align-items: flex-start;
      padding: 0;
    }

    .feedback__block_check p {
      margin: 0 0 0 14px;
    }
  }
}
